
.now-playing-list{list-style-type:none;margin:0;padding:0;width:100%
}
.now-playing-list .now-playing-list-item{background-size:cover !important;background-repeat:no-repeat !important;background-position:center top !important;color:#fff;cursor:pointer;height:33vh;position:relative
}
.now-playing-list .now-playing-list-item .item-overlay{display:-ms-flexbox;display:flex;-ms-flex-align:end;align-items:flex-end;background:linear-gradient(0deg, rgba(0,0,0,0.75) 0%, transparent 70%);padding:20px;position:absolute;top:0;right:0;bottom:0;left:0
}
.now-playing-list .now-playing-list-item .item-overlay .movie-title{margin:0
}



