
*,*::before,*::after{box-sizing:border-box
}
html,body{height:100%;overflow:auto;-webkit-overflow-scrolling:touch;position:relative
}
body{background:#202A33;font-family:'Karla', 'Helvetica Neue', 'Helvetica', 'Arial', sans-serif;font-size:16px;line-height:1.35;min-height:100%
}
.lazyload,.lazyloading{opacity:0
}
.lazyloaded{transition:opacity 250ms cubic-bezier(0.215, 0.61, 0.355, 1);opacity:1
}
.movie-search-container{background:#1D262F;display:-ms-flexbox;display:flex;-ms-flex-align:center;align-items:center;height:60px;padding:0 10px
}
.movie-search-container input[type=text]{-webkit-appearance:none;-moz-appearance:none;appearance:none;transition:all 250ms ease;-ms-flex-positive:1;flex-grow:1;background:transparent;border:none;border-bottom:2px solid #27333f;border-radius:0px;color:#fff;font-size:1rem;height:28px;outline:none
}
.movie-search-container input[type=text]::-webkit-input-placeholder{color:#4e667e
}
.movie-search-container input[type=text]::-moz-placeholder{color:#4e667e
}
.movie-search-container input[type=text]:-ms-input-placeholder{color:#4e667e
}
.movie-search-container input[type=text]::placeholder{color:#4e667e
}
.movie-search-container input[type=text]:focus{border-bottom:2px solid #4e667e
}



